body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;

}

body{
  background-color:aliceblue;
}

.container{
  background-color: white;
  width:140vmin;
  padding: 50px 30px;
  position: absolute;
  transform: translate(-50%, -50%);
  left: 50%;
  top: 50%;
  border-radius: 10px;
  box-shadow: 0 25px 50px rgba(7, 20, 35, 0.2);

}

.options{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 15px;
}

button{
  height: 28px;
  width: 28px;
  display: grid;
  place-items: center;
  border-radius: 3px;
  border: none;
  background-color: white;
  outline: none;
  color: #020929;

}

select{
  padding: 7px;
  border: 1px solid #020929;
  border-radius: 3px;

}

.options label, .options select{
  font-family: "Poppins", sans-serif;

}

.input-wrapper{
  display: flex;
  align-items: center;
  gap: 8px;

}

input [type="color"]{
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance:none ;
  background-color: transparent;
  width: 40px;
  height: 28px;
  border: none;
  cursor: pointer;
}


input [type="color"]::-webkit-color-swatch {
  border-radius: 15px;
  box-shadow: 0 0 0 2px white, 0 0 0 3px #020929;


}

input [type="color"]::-moz-color-swatch {
  border-radius: 15px;
  box-shadow: 0 0 0 2px white, 0 0 0 3px #020929;


}

#text-input{
  margin-top: 10px;
  border: 1px solid #dddddd;
  padding: 20px;
  height: 50vh;
  
}

.active{
  background-color: #e0e9ff;
}
